import React, { useRef, useEffect, useReducer, useCallback } from 'react';
import PropTypes from 'prop-types';
import { Manager, Popper, Reference } from 'react-popper';
import { isRtl, withTheme } from '@zendeskgarden/react-theming';
import { KEY_CODES } from '@zendeskgarden/container-utilities';
import { StyledMenu } from '../styled';
import { getRtlPopperPlacement, getPopperPlacement } from './utils/garden-placements';
import Calendar from './components/Calendar';
import { datepickerReducer, retrieveInitialState } from './utils/datepicker-reducer';
import { DatepickerContext } from './utils/useDatepickerContext';
const Datepicker = props => {
    const { children, placement, popperModifiers, eventsEnabled, zIndex, animate, refKey, value, small, onChange, formatDate, minValue, maxValue, locale, customParseDate } = props;
    const memoizedReducer = useCallback(datepickerReducer({ value, formatDate, locale, customParseDate, onChange }), [value, formatDate, locale, onChange, customParseDate]);
    const [state, dispatch] = useReducer(memoizedReducer, retrieveInitialState(props));
    const scheduleUpdateRef = useRef(undefined);
    const inputRef = useRef(null);
    const isInputMouseDownRef = useRef(false);
    /**
     * Recalculate popper placement while open to allow animations to complete.
     * This must be ran every render to allow for the number of items to change
     * and still be placed correctly.
     **/
    useEffect(() => {
        if (state.isOpen) {
            scheduleUpdateRef.current && scheduleUpdateRef.current();
        }
    });
    /**
     * Dispatch update to reducer when controlled value is changed
     */
    useEffect(() => {
        dispatch({ type: 'CONTROLLED_VALUE_CHANGE', value });
    }, [value]);
    useEffect(() => {
        dispatch({ type: 'CONTROLLED_LOCALE_CHANGE' });
    }, [locale]);
    const popperPlacement = isRtl(props)
        ? getRtlPopperPlacement(placement)
        : getPopperPlacement(placement);
    return (<DatepickerContext.Provider value={{
        state,
        dispatch
    }}>
      <Manager>
        <Reference>
          {({ ref }) => {
        return React.cloneElement(React.Children.only(children), {
            [refKey]: (refValue) => {
                ref(refValue);
                inputRef.current = refValue;
            },
            onMouseDown: () => {
                isInputMouseDownRef.current = true;
            },
            onMouseUp: () => {
                setTimeout(() => {
                    isInputMouseDownRef.current = false;
                }, 0);
            },
            onClick: () => {
                /** Ensure click/focus events from associated labels are not triggered */
                if (isInputMouseDownRef.current && !state.isOpen) {
                    dispatch({ type: 'OPEN' });
                }
            },
            onBlur: () => {
                dispatch({ type: 'CLOSE' });
            },
            onChange: (e) => {
                dispatch({ type: 'MANUALLY_UPDATE_INPUT', value: e.target.value });
            },
            onKeyDown: e => {
                switch (e.keyCode) {
                    case KEY_CODES.ESCAPE:
                    case KEY_CODES.ENTER:
                        dispatch({ type: 'CLOSE' });
                        break;
                    case KEY_CODES.UP:
                    case KEY_CODES.DOWN:
                    case KEY_CODES.SPACE:
                        dispatch({ type: 'OPEN' });
                        break;
                }
            },
            autoComplete: 'off',
            value: state.inputValue
        });
    }}
        </Reference>
        {state.isOpen && (<Popper placement={popperPlacement} modifiers={popperModifiers} 
    // Disable position updating on scroll events while menu is closed
    eventsEnabled={state.isOpen && eventsEnabled}>
            {({ ref, style, scheduleUpdate, placement: currentPlacement }) => {
        scheduleUpdateRef.current = scheduleUpdate;
        const popperStyle = { ...style, zIndex };
        return (<div ref={ref} style={popperStyle} data-garden-id="datepickers.datepicker" date-garden-version={PACKAGE_VERSION}>
                  <StyledMenu animate={state.isOpen && animate} placement={currentPlacement} data-test-id="datepicker-menu" data-test-open={state.isOpen} data-test-rtl={isRtl(props)}>
                    <Calendar small={small} value={value} minValue={minValue} maxValue={maxValue} locale={locale}/>
                  </StyledMenu>
                </div>);
    }}
          </Popper>)}
      </Manager>
    </DatepickerContext.Provider>);
};
Datepicker.propTypes = {
    value: PropTypes.any,
    onChange: PropTypes.any,
    formatDate: PropTypes.func,
    locale: PropTypes.any,
    minValue: PropTypes.any,
    maxValue: PropTypes.any,
    small: PropTypes.bool,
    customParseDate: PropTypes.any,
    refKey: PropTypes.string,
    placement: PropTypes.oneOf([
        'auto',
        'top',
        'top-start',
        'top-end',
        'end',
        'end-top',
        'end-bottom',
        'bottom',
        'bottom-start',
        'bottom-end',
        'start',
        'start-top',
        'start-bottom'
    ]),
    popperModifiers: PropTypes.any,
    animate: PropTypes.bool,
    eventsEnabled: PropTypes.bool,
    zIndex: PropTypes.number
};
Datepicker.defaultProps = {
    placement: 'bottom-start',
    refKey: 'ref',
    animate: true,
    eventsEnabled: true,
    zIndex: 1000,
    locale: 'en-US'
};
export default withTheme(Datepicker);
